@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
.mainbg {
  background: radial-gradient(#f7c96f, #fd8f00);
  background-repeat: no-repeat;
  align-items: center;
  min-height: 800px;
  padding: 30px;
}
/* Float 3 columns side by side */
.column {
  margin-top: 80px;
  display: flex;
  float: left;
  width: 33%;
  overflow: hidden;
  padding: 10px 30px;
}

/* Remove extra left and right margins, due to padding */
.row {
  margin: 0 -5px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 900px) {
  .column {
    width: 100%;
    display: block;
    margin: 20px;
  }
}

/* Style the counter cards */
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 14px;
  text-align: center;
  background-color: #f1f1f1;
  height: 500px;
  border-radius: 30px;
  width: 400px;
}

#img1 {
  background: url(/foodie/static/media/breakfast.4546aec7.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}
#img2 {
  background: url(/foodie/static/media/lunch.c6051ba8.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}
#img3 {
  background: url(/foodie/static/media/dinner.5052953d.jpg);
