}
@media screen and (max-width: 900px) {
  .login {
    margin: auto;
    text-align: center;
    color: #000000;
    box-shadow: 5px 5px 25px 5px #000000;
    border-radius: 8px;
    font-family: "Festive", cursive;
    text-align: center;
    min-height: 600px;
  }
  input[type="text"],
  input[type="submit"] {
    text-align: center;
    padding: 10px;
    margin: 5px 0 20px 0;
    border: none;
    background: #f1f1f1;
    font: 10px "Yomogi", cursive;
    font-family: "Yomogi", cursive;
    border-radius: 10px;
  }
}

.totflex {
